import TarjetaPais from './TarjetaPais.jsx';
import './Mostrar.css';


function Mostrar (lista) {
    const listaPaises = lista.listaPaises;

    return (
        <div className='contenedor-lista'>
          {listaPaises.map((e) => {
            return <TarjetaPais key={e.name} datoPais={e}/>
          })}   
        </div>
    )
}

export default Mostrar;