import './App.css';
import React, { useState, useEffect } from 'react';
import Mostrar from './components/Mostrar';
import Busqueda from './components/Busqueda';


function App() {
  const url = 'https://restcountries.eu/rest/v2/all';
  const [nuevoResultado, setNuevoResultado] = useState([])
  const [Pais, setPais] = useState('');

  const gestorBuscar = (buscar) => {
    setPais(buscar);
  }

  const recuperaDatos = async () => {
    try {
      let respuesta = await fetch(url);
      let resultado = await respuesta.json();
      const busqueda = resultado.filter((element) =>{
        return element.name === Pais;
      })
      if(Pais === ""){
      setNuevoResultado(resultado)
      return resultado;
    } else{
      setNuevoResultado(busqueda)
      return resultado;
    }
    }catch (error) {
    console.log(error)
    }
}

  useEffect(() => {
    recuperaDatos();
  }, [Pais]);

  return (
    <div className="App">
        <div className='cabecera'>
        <h3 className="bg-secondary text-primary text-uppercase p-4">Lista de Paises</h3>
        </div>
        <div className='contenedor-busqueda'>
        <Busqueda onBusqueda={gestorBuscar} />
        </div>
      <div className='contenedor-listado-paises'>
      <Mostrar key={nuevoResultado.id} listaPaises={nuevoResultado}/>
      </div>
    </div>
  );
}

export default App;