import React, { useState } from 'react';

const Busqueda = (props) => {
    const busquedaPais=props.onBusqueda;
    const [error, setError] = useState(false);
    const [nuevoPais, setNuevoPais] = useState('');

    const gestorSubmit = (e) => {
        e.preventDefault();
        setError(false);
        if (nuevoPais.trim() === '')
        {
            setError(true);
			return;
        }
        const buscar = nuevoPais;
        busquedaPais(buscar);
        setNuevoPais('');
      };

      const gestorSearch = (event) => {
        setNuevoPais(event.target.value);
      }

    return (
        <div>
            <h2 className="text-warning mt-5">Indique el país que desea buscar</h2>
            {error ? (
					alert('Debe indicar un país')
				) : null}
            <form onSubmit={gestorSubmit}>
                <div className="form-group p-4">
                <input 
                    onChange={gestorSearch} 
                    className="form-control mt-3 text-center"
                    type="text" 
                    name="busca" 
                    id="busca" 
                    placeholder="País*"  
                    value={nuevoPais} 
                />
                <button 
                    type="submit" 
                    className="btn btn-primary mt-3 w-100">
                        Buscar
                </button>
                </div>
                
            </form>
        </div>
    )
}


export default Busqueda
